import './Cards.scss'
import data from "../../data/data";
import React from "react";
import img from "../../Img/IMAGE7.svg";
import {useNavigate} from "react-router-dom";

function Cards() {
    let navigate = useNavigate();

    function showMoreHandler(productId){
        navigate(`/all-products/${productId}`)
    }
    return (
        <div className="Cards">
            <div className={'set'}>
                {
                    data.cards.map((pr) => {
                        return (
                            <div className={'object'} onClick={()=>{showMoreHandler(pr.id)}}>
                                <img src={pr.img} alt=""/>
                                <div className={'signature'}>
                                    <h3>{pr.name}</h3>
                                    <p>{pr.shortDescription}</p>
                                    <span>{pr.price}</span>
                                </div>
                            </div>
                        )
                    })
                }
            </div>
        </div>
    );
}

export default Cards;
