import './Product.scss'
import {useParams} from "react-router-dom";

function Product(props) {
    let { productId } = useParams();
    return (
        <div className="Card">
            <section className={'left-section-product'}>
                <img src={props.state[productId].img} alt=""/>
            </section>
            <section className={'right-section-product'}>
                <h1>{props.state[productId].name}</h1>
                <span>Bought: {props.state[productId].bought}</span>
                <p>{props.state[productId].description}</p>
                <div className={'price'}>
                    <p>{props.state[productId].price}</p>
                    <button>Buy</button>
                </div>

            </section>
        </div>
    );
}

export default Product;
