import './LatestOffers.scss';
import img from '../../Img/IMAGE3.svg';
import img1 from '../../Img/IMAGE4.svg';
import img2 from '../../Img/IMAGE5.svg';
import img3 from '../../Img/IMAGE6.svg';
import {redirect} from "react-router-dom";

function LatestOffers() {

    return (
        <section className={'LatestOffers'}>
            <h2>Shop our latest offers and categories</h2>
            <p>Lorem ipsum dolor siat amet, consectetur adipiscing elit. Scelerisque in est dui, aliquam, tempor. Faucibus morbi turpis. Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
            <div className={'collections'}>
                <div className={'first-block'}>
                    <div className={'first-top-block'}>
                        <img src={img} alt="" />
                        <span>Laptops</span>
                        <h3>True Laptop Solution</h3>
                    </div>
                    <div className={'first-bottom-block'}>
                        <img src={img1} alt=""/>
                        <div>
                            <span>Watch</span>
                            <h3>Not just stylisht</h3>
                        </div>
                    </div>
                </div>
                <div className={'block second-block'}>
                    <img src={img2} alt=""/>
                    <span>Phones</span>
                    <h3>Your day to day life</h3>
                </div>
                <div className={'block third-block'}>
                    <span>Tablet</span>
                    <h3>Empower your work</h3>
                    <img src={img3} alt=""/>
                </div>
            </div>
        </section>
    );
}

export default LatestOffers;
