import './Items.scss';
import img from '../../Img/IMAGE7.svg';
import img1 from '../../Img/IMAGE8.svg';
import img2 from '../../Img/IMAGE9.svg';
import img3 from '../../Img/IMAGE10.svg';
import { useNavigate } from "react-router-dom";

function Items(props) {
    const navigate = useNavigate();

    const handleClick = () => {
        navigate("all-products");
    }
    return (
        <section className={'Items'}>
            <h2>Save on our most selled items.</h2>
            <p>Our new Limited Edition Winter Design BESPOKE 4-Door Flex</p>
            <div className={'set'}>
                <div className={'object'} onClick={handleClick}>
                    <img src={img} alt=""/>
                    <div className={'signature'}>
                        <h3>{props.state[0].name}</h3>
                        <p>Neque porro quisquam est qui dolorem ipsum quia dolor sit amet</p>
                        <span>{props.state[0].price}</span>
                    </div>
                </div>
                <div className={'object'} onClick={handleClick}>
                    <img src={img1} alt=""/>
                    <div className={'signature'}>
                        <h3>Smart Galaxy Watch 3</h3>
                        <p>Neque porro quisquam est qui dolorem ipsum quia dolor sit amet, consectetur, adipisci
                            velit...</p>
                        <span>$ 199.00 USD</span>
                    </div>
                </div>
                <div className={'object'} onClick={handleClick}>
                    <img src={img2} alt=""/>
                    <div className={'signature'}>
                        <h3>MacBook Air M1</h3>
                        <p>Neque porro quisquam est qui dolorem ipsum quia dolor sit amet, consectetur, adipisci
                            velit...</p>
                        <span>$ 1,009.00 USD</span>
                    </div>
                </div>
                <div className={'object'} onClick={handleClick}>
                    <img src={img3} alt=""/>
                    <div className={'signature'}>
                        <h3>iPad</h3>
                        <p>Neque porro quisquam est qui dolorem ipsum quia dolor sit amet, consectetur, adipisci
                            velit...</p>
                        <span>$ 610.00 USD</span>
                    </div>
                </div>

            </div>
        </section>
    );
}

export default Items;
