import Header from "./Header/Header";
import {Route, Routes} from "react-router-dom";
import data from './data/data'
import Cards from "./Main/Cards/Cards";
import Main from "./Main/Main";
import Product from "./Main/Product/Product";

function App() {
    return (
        <div className="App">
            <Header/>
            <Routes>
                <Route path={'/'} element={<Main state={data}/>}/>
                <Route path={'/all-products'} element={<Cards/>}/>
                <Route path={'/all-products/:productId'} element={<Product state={data.cards}/>}/>
            </Routes>
        </div>
    );
}

export default App;
