import './Header.scss';
import img1 from '../Img/IMAGE.svg';
import img2 from '../Img/Frame.svg';
import {NavLink} from "react-router-dom";

function Header() {
    return (
        <header>
            <NavLink to={'/'}><h1>CALIFORNIA</h1></NavLink>
            <nav>
                <NavLink to={'/all-products'}>ALL PRODUCTS</NavLink>
                <p>SOLUTION</p>
                <p>ABOUT</p>
                <p>SUPPORT</p>
            </nav>
            <div>
                <img src={img1} alt=""/>
                <img src={img2} alt=""/>
            </div>
        </header>
    );
}

export default Header;
