import './Main.scss'
import Carousel from "../Main/arousel/arousel";
import LatestOffers from "../Main/LatestOffers/LatestOffers";
import Items from "../Main/Most-selled-items/Items";
function Main(props) {
    return (
        <main>
            <Carousel/>
            <LatestOffers/>
            <Items state={props.state.cards}/>
        </main>
    );
}

export default Main;
