import './arousel.scss';
import img from '../../Img/Vector.svg';
import img4 from '../../Img/Apple_MacBook-Pro_14-16-inch_10182021_big.jpg.slideshow-xlarge_2x-removebg.png';
import img1 from '../../Img/Vector-1.svg';
import img2 from '../../Img/Rectangle.png';
import img3 from '../../Img/IMAGE1.svg';
import img5 from '../../Img/Snimok-jekrana-20220109-v-18_52_27-transformed.png';
import imgWhite from '../../Img/Rectangle(white).png';
import {useState} from "react";
import {useNavigate} from "react-router-dom";

function Carousel() {
    let [flag, setFlag] = useState(0);
    let navigate = useNavigate();
    function ImgSet (){
        switch (flag){
            case 0: return img3;
            case 1: return img4;
            case 2: return img5;
        }
    }
    function FlagPlus(){
        if (flag === 0){
            setFlag(flag + 1);
        }else if (flag === 1){
            setFlag(flag + 1);
        }else{
            setFlag(0);
        }
    }
    function FlagMinus(){
        if (flag === 0){
            setFlag(2);
        }else if (flag === 2){
            setFlag(flag - 1);
        }else{
            setFlag(0);
        }
    }
    function BottomButton (n){
        switch (flag){
            case n: return imgWhite;
            default: return img2;
        }
    }
    function showMoreHandler(){
        navigate(`/all-products/`)
    }
    return (
        <section className={'Carousel'}>
            <div className={'top-section'}>
                <img src={img} alt="" onClick={FlagPlus}/>
                <div className={'left-section'}>
                    <h1>The new phones are here take a look.</h1>
                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Scelerisque in est dui, aliquam, tempor. Faucibus morbi turpis.</p>
                    <button onClick={showMoreHandler}>Explore</button>
                </div>
                <div className={'right-section'}>
                    <img src={ImgSet()} alt=""/>
                </div>
                <img src={img1} alt="" onClick={FlagMinus}/>
            </div>
            <div className={'bottom-section'}>
                <img src={BottomButton(0)} alt=""/>
                <img src={BottomButton(2)} alt=""/>
                <img src={BottomButton(1)} alt=""/>
            </div>

        </section>
    );
}

export default Carousel;
